.Dashboard {
	width: 100%;
	height: 100%;
}

.Dashboard-photo-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.Dashboard-photo {
	display: grid;
	place-items: center;
}

.Dashboard .fab {
	margin: 0px;
	top: auto;
	right: 20px;
	bottom: 20px;
	left: auto;
	position: fixed;
}
